body, html{
    margin: 0;
    padding: 0;
    height: 100vh;
    font-family: 'Tajawal', sans-serif;

   
}
#root{
    height: 100%;
}
.app1{
    height: 100%;
}
.app{
 display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: flex-start;
	align-items: stretch;
	align-content: stretch;
    height: 100%;
    
}

.img{
    width:33.3%;
    
    margin: 0;
    background: url(Component 3 â€“ 1.svg) no-repeat 100% 100%;
/*
    background-size:cover;
    background-repeat: no-repeat;
    background-position: right;
*/
    position: relative;
     height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-size: cover;

}
#register-txt{
    font-size: 24px;
    color:#FFFFFF;
    
}
.content{
   width:66.6%;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}
.side-img{
    width: 100%;
}

#search-txt{
    text-align: center;
    align-content: center;
    align-items: center;
    justify-content: center;
    font-size: 50px;
    color:white;
    
}

.logo{
    margin-right: 60px;
    align-self: flex-end;
    margin-top:30px;
}
.rect{
    position: absolute;
  
    height: auto;
    opacity: 0.79;
    margin:0;
}
span{
    display: block;
   
}
.rect1{
    position: absolute;

      height: auto;
    opacity: 0.8;
    margin: 0;
}
.center{
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
    justify-content: center;
    height: 100%;
    width:80%;
   
}

.employeer >img{
    color:aliceblue;
}



.reg-text{
    font-size: 28px;    
    text-align: center;
    align-items: center;
    margin: 0 auto;
    line-height: 67px;
    font-weight: bold;
   margin-bottom: 10px;

}


.type-txt{
    font-size: 18px;
    line-height: 35px;
    margin: 0;
    color:#707070;
    margin-bottom: 42px;
}
#lorem{
    font-size: 18px;
    margin-top: 9px;
    margin-bottom: 42px;
}
.choose{
    display: flex;
    
}
.choose-needs{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    
}
.institution{
    border-width: 0.5px;
    border-style: solid;
   
     display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
        width: 162px;
    justify-content: center;
    height: 152px;
    margin-right:20px;
    border-radius: 10px;
}
.employer{
    border-width: 1px;
    border-style: solid;
    margin-right: 15px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
        width: 162px;
    justify-content: center;
    height: 152px;
    border-radius:10px;
}

.employer:hover, .institution:hover{
    
    border-style: solid;
    border-width: 1.5px;
    border-color: #0A78E9;
    
}



.employer:hover  #Path_13, .institution:hover #Path_14, .employer:hover  #Path_72 {
    fill: #0A78E9;
}
.choose-type{
    display: flex;
    flex-direction: row;
    width:80%;
    justify-content: flex-end;

}
.choose-type form label:not(:last-child){
    margin-right: 2%;  
}



#password input:not(:last-child){
    margin-bottom: 20px;
}

.footer{
    
    display: flex;
    flex-direction: column;
    
    width: 100%;
    height: 95px;
   
}

.footer .buttons{
    display: flex;
    justify-content: space-around;
    
}
.next{
    border-width: 1px;
    border-radius: 20px;
    border-style: solid;
background-image: linear-gradient(to right, #2BFFAC, #004EFF);  
 
     width:125.86px;
    height: 31.85px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    color:#fff; 
    
}
#next-txt{
    padding: 4.3 52.2 8.5 40.7;
}
.prev{

    width:126.8px;
    height: 31.85px;
    border-width: 1px;
    border-radius: 20px;
    border-style: solid;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}
form{
    display: flex;
}

.needs-type{
    align-self: flex-end;
    margin-right: 14%;
    font-size: 17px;
    font-weight: bold;
    color:#707070;
    margin-bottom: 23px;
    margin-top: 23px;
}




.birthday{
    display: flex;
    flex-direction: row;
    width:99%;
    margin-bottom: 20px;
    margin-right: 1%;
    
}

.birthday select{
    width:33.2%;
    border: 1px solid #707070;
    border-radius:30px;
    font-size:15px;
    padding-right: 2%;
    padding-top: 1%;
    text-align: right;
    height:43px;
    color:#AAAAAA;
    text-align: left;
}

.birthday select:not(:last-child){
    margin-right: 5px;
}

.name select:not(:last-child), .name input:not(:last-child), .name select:not(:last-child), .name label:not(:last-child){
    margin-right: 5px;
}


.name select{
    width: 50%!important
}
.labl {
    display : flex;
    flex-direction: row;
    
    margin-right: 20px;
   
}
.labl div> input{ /* HIDE RADIO */
   visibility: visible;
    align-self: flex-end;
    
}
.labl + div > input{ /* DIV STYLES */
    cursor:pointer;
    border:1px solid ;
}
.labl > input:checked + div{ /* (RADIO CHECKED) DIV STYLES */
    background-color: #ffd6bb;
    border: 1.5px solid #ff6600;
}

.progress-bar{
    background-image: linear-gradient(to right, #2BFFAC, #004EFF); 
    height: 5px;
}

.progress {
   height:5px;
    margin-bottom: 20px;
}
.user-form{
    
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 50%;
}

.name{
    display: flex;
    flex-direction: row;
    width:100%;
    
}

#name input{
/*    margin-left: 20px;*/
}


@media screen and (max-width: 600px) {
    
    .img{
        display: none;
    }
    .content{
        width: 100%;
    }
    
    .footer{
        width: 100%;
        
        
    }
    .choose-type{
        flex-direction: column;
    }
    .next,.prev{
        width: 20vw;
    }
    .footer p{
        font-size:15px!important
    }
}

@import-normalize;